*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

body{
  background-color: blueviolet;
  display: flex;
  justify-content: center;
  align-items: center;
  width:100vw;
  height:100vh;
}

.App{
  border-radius:10px;
  width:60vw;
  height:90vh;
  border:1px solid black;
  padding:20px;
  background-color: rgb(16, 2, 29);
  color:white;
}

.App h1{
  text-align: center;
  margin-bottom:20px;
  letter-spacing: 5px;
}

.App form{
  margin-bottom:20px;
}

.App form input{
  width:60%;
  font-size:1rem;
  padding:10px;
  border:1px solid rgb(77, 24, 128);
  border-radius: 10px 0px 0px 10px;
}
.App form button{
  width:40%;
  border:1px solid rgb(77, 24, 128);
  font-size:1rem;
  padding:10px;
  font-weight:bold;
  background-color: rgb(77, 24, 128);
  color:white;
  cursor: pointer;
  border-radius:0px 10px 10px 0px;
}

.App .feladatok{
  max-height:60vh;
  overflow: scroll;
  padding:0px 10px;
}
::-webkit-scrollbar {
  width: 0px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb:hover {
  background: rgb(51, 16, 83);
}
::-webkit-scrollbar-thumb {
  background: rgb(77, 24, 128);
  border-radius: 10px;
}
.App .feladatok .feladat{
  display: flex;
  justify-content:space-between;
  align-items: center;
  font-size:1rem;
  background:rgb(77, 24, 128);
  margin:10px 0px;
  padding:10px;
  border-radius:10px;
}
.App .feladatok .feladat i{
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  
 
.App{
  width:100vw;
  height:100vh;
  padding:20px;
  background-color: rgb(16, 2, 29);
  color:white;
  border-radius:0px;
} 

.App form button{
  border:1px solid rgb(77, 24, 128);
  font-size:1rem;
  padding:10px;
  font-weight:bold;
  background-color: rgb(77, 24, 128);
  color:white;
  cursor: pointer;
  border-radius:0px 10px 10px 0px;
}

.App .feladatok{
  max-height:70vh;
  overflow: scroll;
  padding:0px 10px;
}

}